import React, { useState } from "react";
import { Box, Container, Typography, TextField, Paper, Switch, IconButton, Tooltip } from "@mui/material";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import Brightness5Icon from "@mui/icons-material/Brightness5";
import Brightness2Icon from "@mui/icons-material/Brightness2";

// Highlight search matches
const highlightMatch = (text, search) => {
  if (!search) return text;
  const regex = new RegExp(`(${search})`, "gi");
  const parts = text.split(regex);
  return parts.map((part, i) =>
    regex.test(part) ? <span key={i} style={{ backgroundColor: "#ffeb3b" }}>{part}</span> : part
  );
};

// Copy to clipboard helper
const copyToClipboard = (text) => {
  if (navigator.clipboard) {
    navigator.clipboard.writeText(text);
    alert("Copied!");
  } else {
    alert("Clipboard not supported");
  }
};

export default function Dashboard() {
  const [url, setUrl] = useState("");
  const [videoLinks, setVideoLinks] = useState([]);
  const [apiLinks, setApiLinks] = useState([]);
  const [tokens, setTokens] = useState([]);
  const [errors, setErrors] = useState([]);
  const [requests, setRequests] = useState([]);
  const [websockets, setWebsockets] = useState([]);
  const [jsFiles, setJsFiles] = useState([]);
  const [loading, setLoading] = useState(false);
  const [darkMode, setDarkMode] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");

  const handleScrape = async () => {
    if (!url) return alert("Enter a URL first!");
    setLoading(true);

    try {
      const eventSource = new EventSource(`https://145.223.69.84:8181/scrape?urls=${encodeURIComponent(url)}`);

      eventSource.onmessage = (e) => {
        if (e.data === "[DONE]") {
          setLoading(false);
          eventSource.close();
          return;
        }
        try {
          const msg = JSON.parse(e.data);
          if (!msg.entry) return;

          // Video links
          const videos = [];
          ["m3u8", "mpd", "mp4", "ts"].forEach(type => {
            if (Array.isArray(msg.entry[type])) {
              msg.entry[type].forEach(item => {
                if (item.url) videos.push(item.url);
              });
            }
          });

          // API links
          const apis = [];
          if (Array.isArray(msg.entry.apis)) {
            msg.entry.apis.forEach(api => {
              if (api.url) apis.push(api.url);
              if (api.response?.files) {
                Object.values(api.response.files).forEach(u => apis.push(u));
              }
              if (api.tokens) setTokens(prev => Array.from(new Set([...prev, ...api.tokens])));
            });
          }

          // Requests, WebSockets, JS files, Errors
          if (Array.isArray(msg.entry.requests)) setRequests(prev => [...prev, ...msg.entry.requests.map(r => r.url)]);
          if (Array.isArray(msg.entry.websocket)) setWebsockets(prev => [...prev, ...msg.entry.websocket.map(ws => ws.url)]);
          if (Array.isArray(msg.entry.js)) setJsFiles(prev => [...prev, ...msg.entry.js.map(js => js.url)]);
          if (msg.type === "error" && msg.message) setErrors(prev => [...prev, msg.message]);

          setVideoLinks(prev => Array.from(new Set([...prev, ...videos])));
          setApiLinks(prev => Array.from(new Set([...prev, ...apis])));
        } catch(err) {
          console.error("SSE parse error:", err);
        }
      };
    } catch (err) {
      console.error(err);
      alert("Failed to start scraping");
      setLoading(false);
    }
  };

  const filterList = (list) => list.filter(item => item.toLowerCase().includes(searchTerm.toLowerCase()));
  const renderList = (list) =>
    filterList(list).map((item, i) => (
      <Box key={i} sx={{ display: "flex", alignItems: "center", mb: 1, wordBreak: "break-word", fontFamily: "monospace" }}>
        <Box sx={{ flex: 1 }}>{highlightMatch(item, searchTerm)}</Box>
        <Tooltip title="Copy">
          <IconButton size="small" onClick={() => copyToClipboard(item)}>
            <ContentCopyIcon fontSize="small" />
          </IconButton>
        </Tooltip>
      </Box>
    ));

  return (
    <Box
      sx={{
        bgcolor: darkMode ? "#121212" : "#f5f5f5",
        minHeight: "100vh",
        color: darkMode ? "#fff" : "#000",
        py: 3,
      }}
    >
      <Container maxWidth="lg">
        {/* Header with half-moon left and sun right */}
        <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center", mb: 2 }}>
          <Brightness2Icon sx={{ fontSize: 30, mr: 1 }} />
          <Typography variant="h6" sx={{ fontSize: "1.5rem", fontWeight: "bold" }}>#VICE CAPTAIN</Typography>
          <Brightness5Icon sx={{ fontSize: 30, ml: 1 }} />
        </Box>
        <Box sx={{ display: "flex", justifyContent: "flex-end", mb: 2 }}>
          <Switch checked={darkMode} onChange={() => setDarkMode(!darkMode)} color="default" />
        </Box>

        {/* URL input */}
        <Box sx={{ display: "flex", gap: 2, mb: 3 }}>
          <TextField
            fullWidth
            label="Enter URL"
            variant="outlined"
            value={url}
            onChange={(e) => setUrl(e.target.value)}
          />
          <Box>
            <button onClick={handleScrape} disabled={loading}>{loading ? "Scraping..." : "Scrape"}</button>
          </Box>
        </Box>

        {/* Search bar */}
        <TextField
          fullWidth
          label="Search"
          variant="outlined"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          sx={{ mb: 3 }}
        />

        {/* 1. Video Links */}
        <Typography variant="h6">Video Links ({videoLinks.length})</Typography>
        <Paper sx={{ p: 2, maxHeight: "20vh", overflowY: "auto", mb: 3 }}>
          {renderList(videoLinks).length ? renderList(videoLinks) : <Typography>No video links</Typography>}
        </Paper>

        {/* 2. API Links */}
        <Typography variant="h6">API Links ({apiLinks.length})</Typography>
        <Paper sx={{ p: 2, maxHeight: "20vh", overflowY: "auto", mb: 3 }}>
          {renderList(apiLinks).length ? renderList(apiLinks) : <Typography>No API links</Typography>}
        </Paper>

        {/* 3. API Tokens */}
        <Typography variant="h6">API Tokens ({tokens.length})</Typography>
        <Paper sx={{ p: 2, maxHeight: "20vh", overflowY: "auto", mb: 3 }}>
          {renderList(tokens).length ? renderList(tokens) : <Typography>No tokens found</Typography>}
        </Paper>

        {/* 4. Requests */}
        <Typography variant="h6">Requests ({requests.length})</Typography>
        <Paper sx={{ p: 2, maxHeight: "20vh", overflowY: "auto", mb: 3 }}>
          {renderList(requests).length ? renderList(requests) : <Typography>No requests</Typography>}
        </Paper>

        {/* 5. WebSockets */}
        <Typography variant="h6">WebSockets ({websockets.length})</Typography>
        <Paper sx={{ p: 2, maxHeight: "20vh", overflowY: "auto", mb: 3 }}>
          {renderList(websockets).length ? renderList(websockets) : <Typography>No WebSockets</Typography>}
        </Paper>

        {/* 6. JS Files */}
        <Typography variant="h6">JS Files ({jsFiles.length})</Typography>
        <Paper sx={{ p: 2, maxHeight: "20vh", overflowY: "auto", mb: 3 }}>
          {renderList(jsFiles).length ? renderList(jsFiles) : <Typography>No JS files</Typography>}
        </Paper>

        {/* 7. Errors */}
        <Typography variant="h6">Errors ({errors.length})</Typography>
        <Paper sx={{ p: 2, maxHeight: "20vh", overflowY: "auto", mb: 3 }}>
          {renderList(errors).length ? renderList(errors) : <Typography>No errors</Typography>}
        </Paper>

        <Typography variant="body2" align="center" sx={{ mt: 3 }}>
          Created by Vice Captain #Bheki | Credit to ChatGPT | Copyright 2025
        </Typography>
      </Container>
    </Box>
  );
}
